
.recipe-card {
    display: flex;
    flex-direction: column;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    margin: 10px;
    width: 300px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  }
  
  .recipe-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .recipe-info {
    padding: 15px;
  }
  
  .recipe-info h3 {
    margin: 0 0 10px 0;
    font-size: 1.2em;
  }
  
  .recipe-info p {
    margin: 5px 0;
    font-size: 0.9em;
  }
  
  .view-recipe {
    display: inline-block;
    margin-top: 10px;
    padding: 8px 12px;
    background-color: #28a745;
    color: white;
    text-decoration: none;
    border-radius: 4px;
  }
  
  .view-recipe:hover {
    background-color: #218838;
  }
  